import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import Story from './publicStory';

import mu from '../../lib/musher-util';

const initialState = {};

@observer
class StoryPublicFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
    }

    async loadStoriesPublicFeed(props = this.props) {
		const { storyStore } = props.stores;
        const { story } = props;
		await storyStore.loadPublicFeed({ story });
	}

	componentDidMount() {
        this.loadStoriesPublicFeed();
	}

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.story !== this.props.story) {
            this.loadStoriesPublicFeed(nextProps);
        }
    }

    render() {
        const { storyStore, appState } = this.props.stores;
        const { publicFeed } = storyStore;
        const { darkmode } = appState;

        return (
            <div class='w-100'>

                <div class='row pb-4 bg-light'>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 my-1 position-relative rounded rounded-lg py-3 bg-${darkmode ? 'black' : 'white'} shadow-sm`}>
                        {!publicFeed || publicFeed.length === 0 && <>
                            <div class='text-center text-muted mt-5'>
                                <div class='display-1 text-center mb-5'>
                                    <i class='fas fa-comment' />
                                </div>
                                <h3>No story found!</h3>
                            </div>
                        </>}
                        {publicFeed && publicFeed.map((story) => <>
                            <Story
                                stores={this.props.stores}
                                story={story}
                                showAllComments={true}
                            />
                        </>)}
                    </div>
                </div>
            </div>
        );
    }
}

export default StoryPublicFeed;
