import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import linkState from 'linkstate';

import PubSub, { topics } from '../../lib/pubsub';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();
const MARGIN_TOP_BACK = mu.marginTopBack(false);

import StoryAdd from '../../components/story/add';
import StoryPublicFeed from '../../components/story/publicFeed';
import StoryPublicDetail from '../../components/story/publicDetail';
import Ad from '../../components/ad/';
import YrWeather from '../../components/yrweather/';

const initialState = {
	isLoading: false,
	submenu: 'lastWorkouts',
	viewmenu: 'wall',
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { appState, userStore, messageStore } = this.props.stores;
		this.setState({ isLoading: true });
        await appState.getFingerprint();
        await userStore.getInfo();
		await userStore.getNotifications();
        await messageStore.getUnReadMessages();

		const { user } = userStore;
		if (user.currentLocationInfo && user.currentLocationInfo.coords) {
			const { latitude: lat, longitude: lon, altitude } = user.currentLocationInfo.coords;
			userStore.getWeatherUser({ lat, lon, altitude });
		}
		this.setState({ isLoading: false });
    }

	reloadFeeds = async (e) => {
		e.preventDefault();
		this.setState({ isLoading: true });
		this.setState({ isLoading: false });
	}

    sortby = (e) => {
        const { storyStore } = this.props.stores;
        const sortField = e.target.value;
        let sortBy = () => {};
        switch (sortField) {
            case 'likes':
                sortBy = (a, b) => a.likes && b.likes && a.likes.length < b.likes.length;
                break;
            case '-likes':
                sortBy = (a, b) => a.likes && b.likes && a.likes.length > b.likes.length;
                break;
            case 'comments':
                sortBy = (a, b) => a.comments && b.comments && a.comments.length < b.comments.length;
                break;
            case '-comments':
                sortBy = (a, b) => a.comments && b.comments && a.comments.length > b.comments.length;
                break;
            default:
                sortBy = (a, b) => a.id < b.id;
                break;
        }
        storyStore.sortPublicFeed(sortBy);
    }

	componentDidMount() {
		this.loadAll();
	}

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	render() {
        const { isLoading, viewmenu } = this.state;
        const { story, hashtag, teamid, path } = this.props;
		const { userStore, appState, storyStore } = this.props.stores;
        const { darkmode } = appState;
        const team = storyStore.findPublicTeamByUuidv4(teamid);

		const { user } = userStore;
		const { yrWeather, currentLocationGeocode = {} } = user;
		const { formatted_address: place } = currentLocationGeocode;

        const image = mu.getImage({ team });

        let shortname;
        let name;
        let username;
        if (team && team.name) {
            shortname = mu.displayNameShort(team);
            name = mu.displayName(team);
            username = mu.displayName(user);
        } else if (user && user.id) {
            shortname = mu.displayNameShort(user);
            name = mu.displayName(user);
        }

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>

                <div class='bg-light'>
                    <YrWeather stores={this.props.stores} data={yrWeather} place={place} />
                </div>

                <StoryAdd stores={this.props.stores} />

                <div class='row bg-light pb-3 pt-3'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
                        <div class='row'>
                            <div class='col-7 text-overflow'>
                                <h3>
                                    {hashtag && <>
                                        <span class='badge badge-primary'>#{hashtag}</span>
                                    </>}
                                    {team && <>
                                        <div class='d-flex position-relative'>
                                            <div
                                                class={`text-center border rounded-circle imageRounded imageRoundedSmall`}
                                                style={image ? `background-image: url("${image}"); background-size: cover;` : ''}
                                            >
                                                {!image && <div class='text-muted' style='font-size: 20px; padding-top: 3px;'>{shortname}</div>}
                                            </div>
                                            <div class='flex-grow-1 pl-3 text-overflow' style='line-height: 1.2em;'>
                                                <h3 class='mb-1'>
                                                    {team.public ? <>
                                                        <a href={`/teams/public/${team.uuidv4}`} class='stretched-link'>{name}</a>
                                                    </> : <>
                                                        {name}
                                                    </>}
                                                </h3>
                                            </div>
                                        </div>
                                    </>}
                                </h3>
                            </div>
                            <div class='col-5'>
                                <small class='float-right mt-1'>
                                    <select
                                        class='custom-select custom-select-sm'
                                        id='team'
                                        onChange={this.sortby}
                                    >                    
                                        <option value=''><Text id='story.sort.new'>Nyeste først</Text></option>
                                        <option value='likes'><Text id='story.sort.likes-desc'>Flest likes først</Text></option>
                                        <option value='-likes'><Text id='story.sort.likes-asc'>Færrest likes først</Text></option>
                                        <option value='comments'><Text id='story.sort.comments-desc'>Flest kommentarer først</Text></option>
                                        <option value='-comments'><Text id='story.sort.comments-asc'>Færrest kommentarer først</Text></option>
                                    </select>
                                </small>
                            </div>
                        </div>
                    </div>
                </div>

                <StoryPublicFeed stores={this.props.stores} story={story} hashtag={hashtag} teamid={teamid} path={path} />
			</div>
		);
	}
}

export default Home;
